"use client";
import { useDispatch } from "react-redux";
import { useRouter } from "next/navigation";
import { setSearchedQuery } from "@/redux/slices/jobSlice";

const CategoryCarousel = () => {
  const dispatch = useDispatch();
  const router = useRouter();

  const categories = [
    "Frontend Developer",
    "Backend Developer",
    "Data Science",
    "Graphic Designer",
    "FullStack Developer",
  ];

  const searchJobHandler = (query) => {
    dispatch(setSearchedQuery(query));
    router.push(`/browse`);
  };

  return (
    <section className="my-16 px-4">
      <h2 className="text-center text-2xl sm:text-3xl font-semibold mb-6">
        Explore Categories
      </h2>
      <div className="flex flex-wrap justify-center gap-4">
        {categories.map((cat, index) => (
          <button
            key={index}
            onClick={() => searchJobHandler(cat)}
            className="px-4 py-2 text-sm sm:text-base rounded-full border border-gray-300 transition-all hover:bg-gray-200"
          >
            {cat}
          </button>
        ))}
      </div>
    </section>
  );
};

export default CategoryCarousel;
